      'ì´ë¯¸ì§€ í‘¸ì‹œ (ECR ë ˆì§€ìŠ¤íŠ¸ë¦¬)': { ko: 'ì´ë¯¸ì§€ í‘¸ì‹œ (ECR ë ˆì§€ìŠ¤íŠ¸ë¦¬)', en: 'Image push (ECR registry)', ja: 'ã‚¤ãƒ¡ãƒ¼ã‚¸ãƒ—ãƒƒã‚·ãƒ¥ (ECRãƒ¬ã‚¸ã‚¹ãƒˆãƒª)' },
